<template>
  <div class="teacher-info" v-if="teacher">
    <h2 class="ui header">
      {{teacher.username}}
      <span class="text-muted">
        {{teacher.school.name}} - {{SUBJECTS[teacher.subjectId]}} - {{GRADES[teacher.gradeId]}}
      </span>
    </h2>

    <div>
      <div class="basic-row">
        <span class="row-label">手机号</span>
        <div class="row-content">
          {{phoneDisplay}}
          <a class="link link-primary" href="/pad/fetch_change_pwd_code">戳我修改密码</a>
        </div>
      </div>
    </div>

    <p v-if="!teacher.isCourseTeacher">
      任课老师（即关联班级后）方可使用本系统的备课、上课、题库等功能<br>
      <span class="text-danger">你当前还不是任课老师，请联系学校管理员分配班级。</span>
    </p>

    <table class="pad ui small table roles" v-if="hasRole">
      <thead>
        <tr>
          <th class="role-name">角色</th>
          <th>负责范围（不显示归档班级）</th>
        </tr>
      </thead>
      <tbody>
        <tr v-if="teacher.isAdmin">
          <td>System Admin</td>
          <td>--</td>
        </tr>
        <tr v-if="teacher.isManager">
          <td>学校管理员</td>
          <td>全校</td>
        </tr>
        <tr v-if="teacher.isSubjectMaster">
          <td>年级主任</td>
          <td><strong>{{GRADES[teacher.subjectMasterGradeId]}}</strong>年级所有<strong>{{SUBJECTS[teacher.subjectId]}}</strong>教学班</td>
        </tr>
        <tr v-if="teacher.isHeadTeacher">
          <td>班主任</td>
          <td>
            <template v-for="c in teacher.classes">
              <span v-if="c.isNormal" @click="openClassInNewTab(c)" class="link link-primary">{{c.name}}</span>
            </template>
          </td>
        </tr>
        <tr v-if="teacher.isCourseTeacher">
          <td>任课老师</td>
          <td>
            <template v-for="c in teacher.courses">
              <a
                v-if="c.isNormal"
                :href="'/pad/manage/courses/' + c.id "
                target="_blank"
                class="link link-primary">{{c.name}}</a>
            </template>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { SUBJECTS, GRADES } from '@/assets/js/consts/common'
import { pathConvert } from '@/assets/js/utils'

export default {
  name: 'teacher-info',
  data() {
    return {
      SUBJECTS,
      GRADES,
    }
  },
  created() {
  },
  mounted() {
  },
  watch: {
  },
  computed: {
    phoneDisplay() {
      if (!this.teacher) return ''
      if (this.teacher && this.teacher.phone) {
        const phone = this.teacher.phone
        return `${phone.slice(0, 3)}****${phone.slice(-4)}`
      }
      return '未找到'
    },
    hasRole() {
      const t = this.teacher
      return t.isCourseTeacher || t.isHeadTeacher || t.isManager || t.isSubjectMaster || t.isMaster
    },
    ...mapState(['teacher']),
  },
  methods: {
    openClassInNewTab(klass) {
      window.open(pathConvert('management', `classes/${klass.id}`), '_blank')
    },
  },
}
</script>

<style lang="less" scoped>

.ui.header .text-muted {
  font-size: 1rem;
}

.basic-row {
  display: flex;
  align-items: baseline;
  .row-label {
    width: 5em;
    padding: .5em 0;
  }
  .row-content {
    flex: 1;
    padding: .5em 0;
  }
}

.ui.table.roles {
  width: 50em;
  .role-name {
    min-width: 8em;
  }
  .link.link-primary {
    margin-right: 1.5em;
    display: inline-block;
  }
}

</style>



