import {
  CLASS_STATUS_MAP,
  CLASS_STATUS_NORMAL,
  CLASS_STATUS_ARCHIVED,
  CLASS_STATUS_DELETED,
  JUNIOR_HIGH_SCHOOL_GRADES,
  SENIOR_HIGH_SCHOOL_GRADES,
  PRIMARY_GRADES,
  GRADE_HIGH,
  GRADE_JUNIOR,
  GRADE_PRIMARY,
} from '@/assets/js/consts/common'

import Course from 'assets/js/course'


export default class Klass {
  constructor(sourceData) {
    this.id = sourceData.id
    this.name = sourceData.name
    this.gradeId = sourceData.gradeId
    this.status = sourceData.status
    this.year = sourceData.year
    this.defaultPassword = sourceData.defaultPassword
    this.school = sourceData.school
    this.teacher = sourceData.teacher
    this.nStudent = sourceData.nStudent || 0
    this.students = sourceData.students || []

    this.courses = []
    if (sourceData.courses && sourceData.courses.length) {
      sourceData.courses.forEach(data => {
        this.courses.push(new Course(data))
      })
    }
  }

  get statusLabel() {
    return CLASS_STATUS_MAP[this.status]
  }

  get statusColor() {
    if (this.status === CLASS_STATUS_NORMAL) return 'teal'
    if (this.status === CLASS_STATUS_ARCHIVED) return 'grey'
  }

  get bigGrade() {
    if (!this.gradeId) return 0
    if (JUNIOR_HIGH_SCHOOL_GRADES.includes(this.gradeId)) return GRADE_JUNIOR
    if (SENIOR_HIGH_SCHOOL_GRADES.includes(this.gradeId)) return GRADE_HIGH
    if (PRIMARY_GRADES.includes(this.gradeId)) return GRADE_PRIMARY
  }

  get isNormal() {
    return this.status === CLASS_STATUS_NORMAL
  }

  get isArchived() {
    return this.status === CLASS_STATUS_ARCHIVED
  }

  get isDeleted() {
    return this.status === CLASS_STATUS_DELETED
  }
}



