<template>
  <modal v-if="id" @close="muteNotice" class="modal-large" key="feature-notice-modal">
    <h3 slot="header">{{ title }}</span>
    </h3>
    <div slot="body" class="modal-body-container">
      <div class="setting-row" style="white-space: pre-wrap;">
        {{ content }}
      </div>
    </div>
  </modal>
</template>

<script>
import Modal from 'components/modal'
import axios from 'axios'

export default {
  name: 'feature-notice',
  mounted() {
    this.fetchNotice()
  },
  data() {
    return {
      id: null,
      title: null,
      content: null,
    }
  },
  components: {
    Modal,
  },
  methods: {
    fetchNotice() {
      const self = this
      axios.post('/pad/j/notice').then((response) => {
        if (response.data.status === 0) {
          self.id = response.data.featureNotice.pk
          self.title = response.data.featureNotice.title
          self.content = response.data.featureNotice.content
        }
      })
    },
    muteNotice() {
      const self = this
      axios.post(`/pad/j/notice/${this.id}`).then((response) => {
        if (response.data.status === 0) {
          self.id = null
        }
      })
    },
  },
}
</script>

<style lang="less">
@import '~assets/css/base-ui-component.less';
</style>



