import _ from 'lodash'
import Student from './student'
import {
  COURSE_STATUS_NORMAL,
  COURSE_STATUS_ARCHIVED,
  COURSE_STATUS_DELETED,
} from 'assets/js/consts/common'


export default class Course {
  constructor(sourceData) {
    // 初始化的时候往往会忽略学生数据，真正要用的时候再 attach
    this.id = sourceData.id
    this.name = sourceData.name
    this.subjectId = sourceData.subjectId
    this.status = sourceData.status

    this.teacher = sourceData.teacher || null

    this.students = {}
    this.studentAttached = false

    this.groups = []
    this.groupAttached = false
  }

  injectStudentData(data) {
    // 可能有班级学生为空的情况，只要尝试过获取学生就把 studentAttached 设置为 true
    this.studentAttached = true

    for (const sidKey in data) {
      if (this.students.hasOwnProperty(sidKey)) continue
      const s = data[sidKey]
      this.students[sidKey] = new Student(s.id, s.name, s.avatar)
    }
  }

  injectGroupData(data) {
    // 可能有班级学生为空的情况，只要尝试过获取学生就把 studentAttached 设置为 true
    this.groupAttached = true
    if (!data.length) {
      this.groups = []
    } else {
      this.groups = data
    }
  }

  get hasStudent() {
    return !_.isEmpty(this.students)
  }

  get hasGroup() {
    return this.groups.length > 0
  }

  get isNormal() {
    return this.status === COURSE_STATUS_NORMAL
  }

  // 历史原因：关联行政班记得教学班，被删除的话相当于被隐藏，还是可以再次启用。
  get isDeleted() {
    return this.status === COURSE_STATUS_DELETED
  }

  get isArchived() {
    return this.status === COURSE_STATUS_ARCHIVED
  }
}



