import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import Toasted from 'vue-toasted'

import Navbar from 'components/navbar'
import FeatureNotice from 'components/feature-notice'
import Course from '@/assets/js/course'
import Klass from '@/assets/js/class'
import TeacherInfo from './teacher-info'


require('./index.less')

Vue.config.productionTip = false

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'
Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(Vuex)
Vue.use(Toasted, { duration: 1000, position: 'bottom-center' })

const store = new Vuex.Store({
  state: {
    teacher: null,
  },
  mutations: {
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
    },
  },
  actions: {
  },
})


new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  data: {},
  store,
  components: { Navbar, FeatureNotice, TeacherInfo },
  created() {
    const roleRelated = ['isAdmin', 'isManager', 'isHeadTeacher', 'isCourseTeacher', 'isSubjectMaster', 'subjectMasterGradeId']
    const params = {
      fields: ['username', 'subjectId', 'gradeId', 'phone', 'school', 'classes', 'courses'].concat(roleRelated),
    }
    axios.get('/pad/j/teacher_info', { params }).then(resp => {
      const teacher = resp.data.teacher
      teacher.classes = teacher.classes.map(cdata => new Klass(cdata))
      teacher.courses = teacher.courses.map(cdata => new Course(cdata))

      store.commit('updateTeacher', { teacher })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({
          username: teacher.name,
          id: teacher.id,
        })
      }
    })
  },
})



